$.fn.tooltip = function( ) {
    var args = arguments,
        defaults = {
            show_triggers: 'mouseenter',
            hide_trigger: 'mouseleave mousedown',
            message: ( typeof args[0] == 'string' )
                ? args[0]
                : null,
            className: '',
            fx_speed: 100,
            placement: 'bottom',
            offset_top: null,
            touch_enabled: false
        },
        opt = $.extend(defaults, ( typeof args[0] == 'object' )
            ? args[0]
            : args[1] || {});

    // ignore tooltips for touch devices
    if ( Modernizr.touchevents && !opt.touch_enabled )
        return;

    return $( this ).each( function( ) {
        var $node = $( this );

        $node.on( opt.show_triggers, '[data-tooltip]', function( e ) {
            e.preventDefault( );
            $( e.target ).removeAttr( 'title' );
            $( this ).showHint( opt );
        });
    });
};



