$.fn.toContentToggle = function(opt) {
    var staticDefaults = {
        label: '<div class="full button tertiary">' + (opt && opt.label || 'View More') + ' <i class="icon-chevron-down"></i></div>',
        trigger_class: 'block',
        remove_trigger: true
    };

    var opt = $.extend(opt, staticDefaults);

    return this.each(function() {
        $(this).QuickView(opt);
    });

};



