/**
 $.shortString[plugin]
 Truncate a string within a twitter or blog feed and optionally display a button that will reveal the entire feed.
 NOTE: this method does not currently retail html structure
 ****************************************************************************************************/
$.fn.shortString = function(len, opt) {
    var config = $.extend(true, {
        revealText: true,
        elipsis: true,
        showLabel: '[Show all]'
    }, opt || {});

    return this.each(function() {
        // ensure wee're dealing with an integer
        if (!parseInt(len)) {
            return;
        }

        var $this = $(this);
        var fullStr = $this.text().trim();
        var trunc = fullStr.truncate(Math.abs(len));

        if (trunc != fullStr) {
            if (config.elipsis) {
                trunc += '...'
            }
            $this.html(trunc);

            if (config.revealText) {
                var showButton = $('<span class="showMore">' + opt.showLabel + '</span>');
                showButton.click(function() {
                    $this.hide().empty().html(fullStr).fadeIn(200);
                    $(this).remove();
                });
                $this.append(showButton);
            }
        }
    });
};



