$.fn.injectFormLabels = function () {
    return $( this ).each( function () {
        var form = $( this ),
            labels = form.find( 'label' );

        labels.each( function ( i, item, arr ) {
            var label = $( item ),
                input = document.getElementById( label.attr( 'for' ) );

            if ( input && (input.type == 'text' || input.nodeName.toLowerCase() == 'textarea') ) {
                input.setAttribute( 'placeholder', label.text() );
                label.hide();
                // adds input placeholder functionality for legacy browsers
                $( input ).placeholder();
            }
        } );
    } );
};



