/**
 * toggleFieldValue
 * apply default hint text for a text input or text area and toggle on focus/blur
 *
 * @param{String} Default text for form field
 */
$.fn.placeholder = function(text) {
    return $(this).each(function() {
        // defer if html5 placeholder functionality present
        if ('placeholder' in document.createElement('input'))
            return;

        var $input = $(this),
            defaultText = text || this.getAttribute('placeholder') || '';
        if ($input.is(':text, textarea')) {
            $input.val(defaultText = defaultText || '').addClass('empty').bind('focus blur', function(e) {
                switch (e.type) {
                    case 'focus':
                        if (this.value == defaultText) {
                            this.value = '';
                        }
                        break;
                    case 'blur':
                        if (this.value == '') {
                            this.value = defaultText;
                            $input.toggleClass('empty');
                        }
                        break;
                }
            });
        }
    });
};



