$.fn.slidingMenu = function(o) {

    var opt = $.extend(true, {
        menuControlSelector: '.menu-control',
        menuSelector: '.menu',
        triggerSelector: 'ul > li',
        titleSelector: '.menu-title',
        activeClass: 'on',
        currentInViewClass: 'menu-in-view'
    }, o || {});

    return this.each(function() {
        var $menuContainer = $(this),
            $menuControl = $(opt.menuControlSelector),
            $menuTriggers = $menuContainer.find(opt.triggerSelector),
            isAnimating = false,
            menuIndex = 0;


        function handleMenuClick(e) {
            e.stopPropagation();

            if (isAnimating) {
                return;
            }

            var that = this,
                $target = $(e.currentTarget),
                hasMenuItem = $target.find(opt.menuSelector);

            if (hasMenuItem[0]) {
                e.preventDefault();
                this.toggleMenuItem($target, true);
                this.forward();
            } else if ($target.hasClass(opt.titleSelector.substr(1))) {
                e.preventDefault();

                function removeActive() {
                    that.toggleMenuItem($target.parents('li.' + opt.activeClass + ':first'), false);
                    window.removeEventListener(upi.prefixes.transEndEventName, removeActive);
                };

                // if csstransitions, wait until finished before removing activeClass
                (Modernizr.csstransitions) ?
                window.addEventListener(upi.prefixes.transEndEventName, removeActive, false): removeActive();

                this.back();
            } else {
                // reached a bottom-level menu item or link
                hideMenu();
            }
        }

        function toggleMenu(e) {
            this[$menuContainer.hasClass(opt.activeClass) ? 'hideMenu' : 'showMenu']();
        }

        // set scroll position to top of menu trigger
        function setScroll() {
            $menuControl.scrollToElement({
                effect_speed: 300
            });
        }

        function showMenu() {
            $menuControl.addClass(opt.activeClass);
            $menuContainer.addClass(opt.activeClass)
                .trigger('menu:open');
        }

        function hideMenu() {
            $menuControl.removeClass(opt.activeClass);
            $menuContainer.removeClass(opt.activeClass)
                .trigger('menu:close');
        }

        function resetMenu(e) {
            menuIndex = 0;
            $menuTriggers.removeClass(opt.activeClass);
            $menuContainer.css(Modernizr.prefixed('transform'), 'translate(0,0)');
            clearAnimating();
        }

        function clearAnimating() {
            isAnimating = false;
        }

        return {

            _initialize: function() {
                //                    console.log( 'init slider' );
                $menuContainer.on('click', opt.triggerSelector, handleMenuClick.bind(this));
                $menuContainer.on('menu:close', resetMenu);
                $menuContainer.on('menu:show', this.showMenu);
                $menuContainer.on('menu:hide', this.hideMenu);
                $menuControl.on('click', toggleMenu.bind(this));
                $menuContainer.on('menu:destroy', this.destroy.bind(this));
            },

            destroy: function() {
                //                    console.log( 'destroy' );
                this.hideMenu();
                $menuContainer.off('click');
                $menuContainer.off('menu:open');
                $menuContainer.off('menu:close');
                $menuContainer.off('menu:show');
                $menuContainer.off('menu:hide');
                $menuContainer.off('menu:destroy');
                $menuControl.off('click');
            },

            showMenu: showMenu,

            hideMenu: hideMenu,

            forward: function() {
                menuIndex++;
                this.slideNav('forward');
            },

            back: function() {
                menuIndex--;
                this.slideNav('back');
            },

            toggleMenuItem: function($item, doShow) {
                $menuContainer.removeClass(opt.currentInViewClass);
                $menuTriggers.removeClass(opt.currentInViewClass);

                if (doShow) {
                    // forward navigation
                    $item.addClass(opt.currentInViewClass);
                } else {
                    // backward navigation
                    $item.parents('.on:first').addClass(opt.currentInViewClass);
                }
                $item.toggleClass(opt.activeClass, doShow);
            },

            slideNav: function(direction) {
                (Modernizr.csstransitions) ?
                window.addEventListener(upi.prefixes.transEndEventName, function clearAnimationLock() {
                    clearAnimating();
                    window.removeEventListener(upi.prefixes.transEndEventName, clearAnimationLock, false);
                }, false): isAnimating = false;

                isAnimating = true;
                $menuContainer.css(Modernizr.prefixed('transform'), 'translate(' + -(100 * menuIndex) + '%,0)');
                setScroll();
            }


        }._initialize();
    });
};



